  padding: 8px 30px;
  width: 240px;
  height: 38px;
  border: 1px solid #a6775b;
  border-radius: 5px;
  font-weight: 500;
  font-size: 16px;
  color: #a6775b; }

.box {
  display: flex;
  flex-direction: column;
  align-items: center; }

.form-wrapper {
  max-width: 580px;
  width: 100%; }

.form {
  background: #fafafa;
  /* Image Card */
  padding: 38px 73px 50px 68px;
  max-width: 580px;
  width: 100%;
  box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.07);
  border-radius: 0 0 10px 10px;
  display: none;
  flex-direction: column; }
  .form.active {
    display: flex; }
  .form__text {
    font-size: 14px;
    line-height: 140%;
    margin-bottom: 18px;
    color: rgba(63, 65, 66, 0.4); }
  .form__button {
    margin-top: 40px;
    display: inline-block;
    padding: 8px 13px;
    width: 100%;
    height: 38px;
    /* Accent */
    font-weight: 500;
    font-size: 16px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #a6775b;
    border-radius: 5px; }
  .form label {
    margin-bottom: 20px; }
    .form label a {
      display: flex;
      justify-content: flex-end;
      font-size: 14px;
      margin-top: 13px;
      color: rgba(63, 65, 66, 0.4); }
    .form label span {
      font-size: 14px;
      color: #1d1d1d;
      margin-bottom: 12px;
      display: inline-block; }
      .form label span i {
        color: #ee5a5a; }
    .form label input {
      padding: 17px 21px 17px 15px;
      display: inline-block;
      max-width: 439px;
      width: 100%;
      height: 38px;
      font-size: 14px;
      color: #3f4142;
      border: 2px solid #e8e8e8;
      box-sizing: border-box; }

.form__tab {
  display: flex;
  max-width: 580px;
  width: 100%;
  height: 46px; }

.form__tab-button {
  width: 34%;
  background: #efefef; }
  .form__tab-button.active {
    background: #fafafa; }
  .form__tab-button:nth-child(1) {
    border: 10px 0 0 0; }

.select__box {
  display: flex;
  justify-content: flex-start;
  width: 50%; }

.select__wrapper {
  position: relative;
  z-index: 21;
  width: 100%;
  max-width: 280px;
  margin-right: 20px; }
  .select__wrapper:last-child {
    margin-right: 0; }

.select__header {
  padding: 8px 20px;
  height: 38px;
  border: 1px solid rgba(63, 65, 66, 0.7);
  border-radius: 5px;
  max-width: 280px;
  width: 100%;
  position: relative; }
  .select__header span {
    pointer-events: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .select__header.active::after {
    transform: rotate(0deg); }
  .select__header::after {
    content: "";
    position: absolute;
    right: 20px;
    background-image: url(../images/appereance.svg);
    width: 10px;
    height: 5px;
    top: 15px;
    transform: rotate(-90deg);
    transition: 0.3s all; }

.select__header-title {
  font-size: 16px;
  line-height: 120%;
  color: rgba(63, 65, 66, 0.7); }

.select__header-pick {
  font-size: 16px;
  line-height: 120%;
  color: rgba(63, 65, 66, 0.7);
  font-weight: 500; }

.select__dropdown {
  background-color: #e5e5e5;
  box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.07);
  border-radius: 10px;
  max-width: 280px;
  width: 100%;
  border: 1px solid black;
  overflow: hidden;
  position: absolute;
  opacity: 0;
  visibility: hidden;
  z-index: 21; }
  .select__dropdown.active {
    opacity: 1;
    visibility: visible; }

.select__dropdown-item {
  border-bottom: 1px solid black;
  font-size: 16px;
  line-height: 150%;
  padding: 8px 20px;
  color: rgba(63, 65, 66, 0.7);
  transition: 0.3s all; }
  .select__dropdown-item:hover {
    background-color: #fafafa; }
  .select__dropdown-item:last-child {
    border: 0; }

.settings {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 22px; }

.breadcrumbs {
  width: 50%; }
  .breadcrumbs ul li {
    display: inline;
    color: rgba(63, 65, 66, 0.7); }

.pagination {
  justify-content: flex-end;
  display: flex;
  align-items: center;
  width: 50%; }

.pagination__counter {
  font-size: 16px;
  margin-right: 35px;
  color: rgba(0, 0, 0, 0.6); }

.pagination__select {
  margin: 0 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 104px;
  width: 100%;
  padding: 16px 12px 16px 16px;
  display: flex;
  height: 39px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px; }
