.header {
    background-color: transparent;
    padding-top: 67px;
    padding-bottom: 65px;
    z-index: 3;
    position: fixed;
    top: 0;
    transition: 0.3s all;
    width: 100%;
    &.active {
        z-index: 100;

        background-color: #fff;
        box-shadow: 0 6px 4px -4px rgba(0, 0, 0, 0.2);
    }

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__logo {
        display: flex;
        align-items: center;
        &-item:first-child {
            margin-right: 13px;
        }
        margin-right: 57px;
    }
    &__nav {
    }
    &__list {
        display: flex;
        &-item {
            margin-right: 20px;
            &:last-child {
                margin-right: 0;
            }
            &:nth-child(3),
            &:nth-child(5) {
                .header__list-link {
                    &:hover,
                    &.active {
                        color: #3f4142;
                        &::after {
                            width: 89%;
                        }
                    }
                }
            }
        }
        &-link {
            font-size: 16px;
            color: rgba(63, 65, 66, 0.7);
            transition: 0.3s all;
            position: relative;
            &:hover,
            &.active {
                color: #3f4142;
                &::after {
                    width: 80%;
                }
            }
            &::after {
                content: "";
                position: absolute;
                bottom: -2px;
                left: 50%;
                transform: translateX(-50%);
                width: 0;
                height: 1px;
                background-color: #3f4142;
                transition: 0.3s all;
            }
        }
    }
    &__tel {
        font-family: "Lato", sans-serif;
        font-weight: 900;
        font-size: 18px;
        color: $brown;
        position: relative;
        &::after {
            content: "";
            position: absolute;
            bottom: -2px;
            left: 0;
            width: 100%;
            height: 2px;
            background-color: $brown;
        }
    }
}
