.second {
    &.header {
        position: relative;
    }
    .header__container {
        justify-content: flex-start;
    }
    .nav-second {
        margin-left: 68px !important;
    }
    .nav__profile {
        display: flex;
        align-items: center;
        margin-left: auto;
    }
    .nav__profile-counter {
        margin-right: 16px;
        font-size: 16px;
        line-height: 120%;
        color: #3f4142;
        span {
            color: $brown;
        }
    }
    .nav__profile-button {
        width: 143px;
        height: 38px;
        @include flex-center;
        border: 1px solid #a6775b;
        box-sizing: border-box;
        border-radius: 5px;
        font-weight: 500;
        font-size: 16px;
        color: $brown;
        margin-right: 47px;
    }
    .nav__profile-login {
        font-size: 16px;
        color: #3f4142;
        margin-right: 16px;
    }
    .nav__profile-icon {
        object-fit: cover;
        object-position: center;
        width: 32px;
        height: 32px;
    }
}
